import React from "react";
import {getReactTypeName} from "hui/core/utils";

export default class HAZeroState extends React.Component {
    static propTypes = {
        titleText: React.PropTypes.string,
        subTitleText: React.PropTypes.string,
        buttonText: React.PropTypes.string,
        onButtonClick: React.PropTypes.func,
        simulateViewport: React.PropTypes.bool,
        children: function(props, propName, componentName) {
            let prop = props[propName] || [],
                types = ["HASection", "HAHeader", "HAFooter"],
                typeName;

            prop = Array.isArray(prop) ? prop : [prop];
            for (let child in prop) {
                if (prop[child] && prop[child].type) {
                    typeName = getReactTypeName(prop[child]);
                    // Only accept a single child, of the appropriate type
                    if (types.indexOf(typeName) === -1) {
                        return new Error(`${componentName}'s children can only have one instance of the following types: ${types.join(", ")}`);
                    } else {
                        types[types.indexOf(typeName)] = "";
                    }
                }

            }
        }
    };

    // TODO this code is a temporary hack to set the viewport. This will be removed once its made available as a the plugin config.
    componentWillMount() {
        if (this.props.simulateViewport) {
            const viewports = document.getElementsByName("viewport");

            if (viewports && viewports.length>0) {
                viewports[0].content = "width=device-width, initial-scale=1";
            }
        }
    }

    // TODO this code is a temporary hack to set the viewport. This will be removed once its made available as a the plugin config.
    componentWillUnmount() {
        if (this.props.simulateViewport) {
            const viewports = document.getElementsByName("viewport");

            if (viewports && viewports.length>0) {
                viewports[0].content = "";
            }
        }
    }

    renderFooter(HAFooter) {
        return(
            <div className="footer zero-state-footer">
                {HAFooter}
            </div>
        );
    }

    render() {

        let button, HAHeader, HASection, HAFooter, h3, typeName;

        if (this.props.buttonText) {
            button = <button className="ha-button-primary ha-button" onClick={this.props.onButtonClick}>{this.props.buttonText}</button>;
        }

        if (this.props.subTitleText) {
            h3 = <h3 className="sub-title">{this.props.subTitleText}</h3>;
        }

        if (this.props.children) {
            React.Children.forEach(this.props.children, child => {
                if (!React.isValidElement(child)) {
                    return;
                }
                typeName = getReactTypeName(child);
                if (typeName === "HAHeader") {
                    HAHeader = child;
                }
                if (typeName === "HASection") {
                    HASection = child;
                }
                if (typeName === "HAFooter") {
                    HAFooter = child;
                }

            });
        }

        return (
            <div className="ha-zero-state-container">
                <div className="image-container zero-state-header">
                    {HAHeader}
                </div>
                <div className="zero-state-content">
                    <div>
                        <h1 className="main-title">{this.props.titleText}</h1>
                        {h3}
                    </div>
                    <div>
                        {HASection}
                    </div>
                    <div>{button}</div>
                </div>
                {HAFooter && this.renderFooter(HAFooter)}
            </div>
        );
    }
}
